@mixin desktop-xxl {
	@media (min-width: $breakpoint-desktop-xxl) {
		@content;
	}
}

@mixin desktop-xl {
  @media (min-width: $breakpoint-desktop-xl) {
	@content;
  }
}


@mixin desktop {
	@media (min-width: $breakpoint-desktop) {
		@content;
	}
}


@mixin tablet {
	@media (min-width: $breakpoint-tablet) {
		@content;
	}
}